import React, { useEffect, useState } from 'react';
import './App.css';
import axios from 'axios';
import { useSearchParams } from "react-router-dom";
import { MapContainer, TileLayer, Marker, Popup, Polyline } from 'react-leaflet';
import polyline from "@mapbox/polyline";






function App() {

  interface Node {
    activityPositions: any;
    activityName: string;
  }


  const [nodes, setNodes] = useState<Node[]>([]);

  const nightRuns: number[] = []
  const morningRuns: number[] = []
  const afternoonRuns: number[] = []
  const eveningRuns: number[] = []


  const clientID = "71922";
  const clientSecret = "3f0f2cd9d0d2a294ba66ff9c83ebde5d82b59746";
  const refreshToken = "your refresh token";
  const auth_link = "https://www.strava.com/oauth/token";
  const activities_link = `https://www.strava.com/api/v3/athlete/activities`;


  let params = new URLSearchParams(window.location.search);

  const code = params.get("code");



  useEffect(() => {
    async function fetchData() {
      const stravaAuthResponse = await axios.all([
        axios.post(`${auth_link}?client_id=${clientID}&client_secret=${clientSecret}&code=${code}&grant_type=authorization_code`)
      ]);

      const stravaActivityResponse = await axios.get(`${activities_link}?per_page=200&access_token=${stravaAuthResponse[0].data.access_token}`);
      
      const polylines = [];
      for (let i = 0; i < stravaActivityResponse.data.length; i += 1) {
        try{
          
        const activity_polyline = stravaActivityResponse.data[i].map.summary_polyline;
        const activity_name = stravaActivityResponse.data[i].name;

        if (activity_name == "Morning Run"){
          morningRuns[morningRuns.length] = stravaActivityResponse.data[i].average_speed;
        }
        if (activity_name == "Afternoon Run"){
          afternoonRuns[afternoonRuns.length] = stravaActivityResponse.data[i].average_speed;
        }
        if (activity_name == "Evening Run"){
          eveningRuns[eveningRuns.length] = stravaActivityResponse.data[i].average_speed;
        }
        if (activity_name == "Night Run"){
          nightRuns[nightRuns.length] = stravaActivityResponse.data[i].average_speed;
        }
        polylines.push({activityPositions: polyline.decode(activity_polyline), activityName: activity_name});
        }catch{
          continue
        }
      }
      setNodes(polylines);
      console.log("morningRuns runs=",(((morningRuns.reduce((a, b) => a + b, 0))/morningRuns.length-1))/16.667)
      console.log("afternoon runs=",(((afternoonRuns.reduce((a, b) => a + b, 0))/afternoonRuns.length-1))/16.667)
      console.log("eveningRuns runs=",(((eveningRuns.reduce((a, b) => a + b, 0))/eveningRuns.length-1))/16.667)
      console.log("afternoon runs=",(((nightRuns.reduce((a, b) => a + b, 0))/nightRuns.length-1))/16.667)

    }


    fetchData();
  }, []);

  
  console.log(afternoonRuns.length)


  



  return (
    <MapContainer center={[51.505, -0.09]} zoom={13}>
      <TileLayer
        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
      />
 {nodes.map((activity, i) => (
        <Polyline key = {i} positions={activity.activityPositions}>
          <Popup>
            <div>
              <h2>{"Name: " + activity.activityName}</h2>
            </div>
          </Popup>
        </Polyline>
      ))}
    </MapContainer>





  );

}

export default App;
